<template>
  <transition name="fade">
    <div
      v-if="active"
      :id="eventName"
      class="fixed inset-0 z-[9999] flex h-screen w-full items-center justify-center overflow-y-auto"
      :class="customOverlay"
      @click.self="closeModal()"
    >
      <div
        class="fixed bottom-0 overflow-y-auto shadow-lg nexus:relative"
        :class="customClass"
      >
        <slot />
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    active: {
      required: true,
      type: Boolean,
    },
    eventName: {
      required: true,
      type: String,
    },
    preventClose: {
      required: false,
      type: Boolean,
    },
    customStyle: {
      required: false,
      type: [String, Array],
      default: "",
    },
    customBackground: {
      required: false,
      type: String,
      default: "",
    },
    fullScreen: {
      required: false,
      type: String,
      default: "",
    },
    customVerticalPadding: {
      required: false,
      type: String,
      default: "",
    },
    customHorizontalPadding: {
      required: false,
      type: String,
      default: "",
    },
    customPadding: {
      required: false,
      type: String,
      default: "",
    },
    customWidth: {
      required: false,
      type: String,
      default: "",
    },
    customHeight: {
      required: false,
      type: String,
      default: "",
    },
    customBorderRadius: {
      required: false,
      type: String,
      default: "",
    },
    customOverlay: {
      required: false,
      type: String,
      default: "bg-bg-overlay",
    },
  },
  computed: {
    customClass() {
      return [
        this.widthClass,
        this.paddingClass,
        this.heightClass,
        this.roundedClass,
        this.backgroundClass,
      ];
    },
    widthClass() {
      let output = "w-full nexus:w-[448px]";
      if (this.customWidth) {
        output = this.customWidth;
      }
      return output;
    },
    heightClass() {
      let output = "min-h-[268px]";
      if (this.customHeight) {
        output = `min-h-[268px] ${this.customHeight}`;
      }
      if (this.fullScreen === "mobile") {
        output = this.customHeight
          ? `min-h-[268px] h-[100dvh] ${this.customHeight}`
          : `min-h-[268px] h-[100dvh] nexus:h-auto`;
      } else if (this.fullScreen === "nexus") {
        output = this.customHeight
          ? `min-h-[268px] h-[100dvh] ${this.customHeight}`
          : `min-h-[268px] h-[100dvh] ipad:h-auto`;
      } else if (this.fullScreen === "ipad") {
        output = `min-h-[268px] h-[100dvh]`;
      }
      return output;
    },
    paddingClass() {
      let output =
        "py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-300 px-spacing-200 nexus:px-spacing-250 ipad:px-spacing-300";
      if (this.customPadding) {
        output = this.customPadding;
      } else if (this.customVerticalPadding || this.customHorizontalPadding) {
        output = `${this.customVerticalPadding} ${this.customHorizontalPadding}`;
      }
      return output;
    },
    backgroundClass() {
      let output = "bg-bg-neutral-default";
      if (this.customBackground) {
        output = this.customBackground;
      }
      return output;
    },
    roundedClass() {
      let output = `rounded-t-08 nexus:rounded-b-08`;
      if (this.fullScreen === "ipad") {
        output = "";
      } else if (this.fullScreen === "nexus") {
        output = `ipad:rounded-t-08 ipad:rounded-b-08`;
      } else if (this.fullScreen === "mobile") {
        output = `nexus:rounded-t-08 nexus:rounded-b-08`;
      } else if (this.customBorderRadius) {
        output = this.customBorderRadius;
      }
      return output;
    },
  },
  watch: {
    active(newValue) {
      if (newValue) {
        document.addEventListener("keyup", this.handleKeyUp);
        document.body.style.cssText = "overflow:hidden";
      } else {
        document.removeEventListener("keyup", this.handleKeyUp);
        document.body.style.cssText = "";
      }
    },
  },
  methods: {
    handleKeyUp(event) {
      const escPressed = event?.key === "Escape";
      const element = document.getElementById(this.eventName);
      if (escPressed && this.active) {
        this.closeModal();
        document.removeEventListener("keyup", this.handleKeyUp);
      }
      if (!this.active || !element) {
        document.removeEventListener("keyup", this.handleKeyUp);
      }
    },
    closeModal() {
      if (!this.preventClose) {
        document.removeEventListener("keyup", this.handleKeyUp);
        this.$bus.$emit(this.eventName, false);
      }
    },
  },
  beforeDestroy() {
    document.body.style.cssText = "";
  },
};
</script>
