<template>
  <Modal
    :active="showModal"
    :event-name="'product-select-modal'"
    :custom-width="'w-full ipad:w-[768px] ipadPro:w-[1000px]'"
    :full-screen="'nexus'"
    :custom-padding="'p-spacing-0'"
    :prevent-close="true"
  >
    <GeneralForm
      :heading="'PRODUCTS'"
      :sub-heading="'Click on the products to add'"
      :show-submit-button="true"
      :submit-button-label="'Confirm'"
      @close-pressed="handleClose"
      @form-submitted="handleSubmit"
      class="flex-1"
    >
      <template #form-body>
        <div class="flex h-full min-h-[500px] w-full flex-col">
          <div
            class="flex flex-col-reverse items-center gap-spacing-100 ipad:flex-row ipad:gap-spacing-200"
          >
            <Search
              placeholder="Search..."
              @search="searchByItemName"
              label="Name"
              search-id="commercial-item-name-search"
              class="mb-spacing-200 ipadPro:max-w-[400px]"
            />
            <div class="mb-spacing-200 flex items-center gap-spacing-150">
              <GeneralButton
                :button-type="'button'"
                :type="'success'"
                :size="'small'"
                :emphasis="'secondary'"
                :label="'Refresh'"
                :icon-slot="true"
                :icon-position="'leading'"
                @click.native="getCategoryList()"
              >
                <template #leading-icon>
                  <i
                    class="fa-solid fa-refresh relative bottom-spacing-50"
                    :class="isLoading ? 'animate-spin' : ''"
                  ></i>
                </template>
              </GeneralButton>
              <GeneralButton
                :button-type="'button'"
                :type="'brand'"
                :size="'small'"
                :emphasis="'secondary'"
                :label="'Expand'"
                :icon-slot="true"
                :icon-position="'leading'"
                @click.native="expandTreeView()"
              >
                <template #leading-icon>
                  <i class="fa-solid fa-expand relative bottom-spacing-50"></i>
                </template>
              </GeneralButton>

              <GeneralButton
                :button-type="'button'"
                :type="'brand-secondary'"
                :size="'small'"
                :emphasis="'secondary'"
                :label="'Collapse'"
                :icon-slot="true"
                :icon-position="'leading'"
                @click.native="collapseTreeView()"
                ><template #leading-icon>
                  <i
                    class="fa-solid fa-compress relative bottom-spacing-50"
                  ></i>
                </template>
              </GeneralButton>
            </div>
          </div>
          <div v-for="(child, index) in data" :key="child?._id">
            <SelectableTreeView
              class="mb-spacing-150"
              :node-obj="child"
              :level="Number(0)"
              :index="index"
              :expanded-tree-view="expandedTreeView"
              :search-item-name="searchItemName"
              @select-product="handleSelectProduct"
              :selected-products="selectedProducts"
            />
          </div>
        </div>
      </template>
    </GeneralForm>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
import { mapActions } from "vuex";
export default {
  components: {
    Modal,
    SelectableTreeView: () =>
      import("@/components/Category/SelectableTreeView.vue"),
    GeneralForm: () => import("@/components/General/Form.vue"),
    Search: () => import("@/components/General/Search.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  data() {
    return {
      showModal: false,
      data: [],
      selectedProducts: [],
      expandedTreeView: { expand: true },
      searchItemName: "",
      isLoading: false,
      modalHandler: null,
      id: null,
    };
  },
  mounted() {
    this.modalHandler = (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.getCategoryList();
        this.selectedProducts = data?.products || [];
        this.id = data?.id;
      } else {
        this.showModal = false;
        this.selectedProducts = [];
        this.id = null;
      }
    };
    this.$bus.$on("product-select-modal", this.modalHandler);
  },
  methods: {
    ...mapActions("category", ["getCommercialItems"]),
    async expandTreeView() {
      this.expandedTreeView = { expand: true };
    },
    async collapseTreeView() {
      this.expandedTreeView = { expand: false };
    },
    async getCategoryList() {
      this.isLoading = true;
      this.$bus.$emit("showLoader", {
        showLoader: true,
      });
      const response = await this.getCommercialItems({
        params: { mode: "FULL" },
      });
      if (response?.success) {
        this.data = response?.data;
      } else {
        this.$toast.error("Unable to load data");
      }
      this.$bus.$emit("showLoader", false);
      this.isLoading = false;
    },
    searchByItemName(query) {
      this.searchItemName = query;
      this.expandedTreeView = { expand: true };
    },
    handleClose() {
      this.$bus.$emit("product-select-modal", false);
    },
    handleSelectProduct(product) {
      const isAdded = this.selectedProducts?.some(
        (p) => p?._id === product?._id,
      );
      if (isAdded) {
        this.selectedProducts = this.selectedProducts?.filter(
          (p) => p?._id !== product?._id,
        );
      } else {
        this.selectedProducts = [...this.selectedProducts, product];
      }
    },
    handleSubmit() {
      this.$bus.$emit("selected-products-from-product-select", {
        products: [...this.selectedProducts],
        id: this.id,
      });
      this.handleClose();
    },
  },
  beforeDestroy() {
    this.$bus.$off("product-select-modal", this.modalHandler);
    this.modalHandler = null;
  },
};
</script>
